.block-three{
  width: 320px;
  height: 280px;
}

.block-headline-title{
  width: 100%;
  filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.1));
  @include blocksTitle();
  .content{
    background-color: white;
    box-sizing: border-box;
    border-radius: 0px 0px 6px 6px;
  }
}

.small-block{
  width: 320px;
  font-size: 14px;
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.1));
  @include blocksTitle();
  .content{
    background-color: white;
    border-bottom: 1px solid #DDDDDD;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    .slider{
      box-sizing: border-box;
      display: flex;
      align-items: center;
      height: 170px;
      .img{
        width: 24px;
        height: 24px;
      }
      // fist box
      p{
        text-align: center;
        margin: 58px 8px;
        span{
          padding: 3px 8px;
          background-color: #FFEEE6;
          color: #FF4C00;
          font-weight: bold;
          border-radius: 6px;
        }
      }
      //there is the new box with events
      .event{
        box-sizing: border-box;
        margin: 34px auto;
        .calendar{
          width: 40px;
          height: 40px;
          border: 1px solid #DDDDDD;
          box-sizing: border-box;
          border-radius: 8px;
          overflow: hidden;
          margin: 0 auto;
          justify-content: space-between;
          .month{
            background-color: #DE2F2F;
            color: white;
            text-align: center;
          }
          .day{
            text-align: center;
          }
        }
        .date-type-event{
          font-size: 13px;
          color: #616061;
          margin-top: 10px;
        }
        .event-exact{
          margin-top: 26px;
        }
      }
      //there is knowledge block
      .knowledge{
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        .book{
          width: 48px;
          margin: auto;
          margin-bottom: 7px;
          background-blend-mode: color, normal;
        }
        p{
          margin: 0;
        }
      }
    }
    .radios{
      @include flexCenter();
      margin-bottom: 18px;
      .radio{
        margin: 5px;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #DDDDDD;
        &.active{
          background-color: #FF4C00;
        }
      }
    }
  }
  .more{
    padding: 10px 22px;
    display: flex;
    align-items: center;
    border-radius: 0px 0px 6px 6px;
    color: #FF4C00;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 13px;
    background-color: white;
    .arrow{
      margin-left: 20px;
      color: #FF4C00 !important;
    }
  }
}

.news{
  width: 660px;
  font-size: 14px;
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.1));
  @include blocksTitle();
  .content{
    background-color: white;
    border-bottom: 1px solid #DDDDDD;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    height: 700px;
    .notifications-unread{
      .unread-block{
        position: relative;
        height: 23px;
        border-bottom: 1.5px solid #FF4C00;
        .unread-title{
          position: relative;
          background-color: white;
          padding: 5px 23px;
          color: #FF4C00;
          border: 1.5px solid #FF4C00;
          border-radius: 4px;
          left: 20px;
          top: 13px;
        }
      }
    }
    .notifications-yesterday{
      .yesterday-block{
        position: relative;
        height: 0px;
        .yesterday-title{
          position: relative;
          background-color: white;
          padding: 5px 23px;
          color: #FF4C00;
          border: 1px solid #DDDDDD;
          border-radius: 4px;
          left: 20px;
          top: -10px;
        }
      }

    }
  }
  .more{
    height: 40px;
    border-radius: 0px 0px 6px 6px;
    background-color: white;
  }

}

.notifications-block{
  width: 320px;
  font-size: 14px;
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.1));
  @include blocksTitle();
  .content{
    background-color: white;
    border-bottom: 1px solid #DDDDDD;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    padding: 1px 21px;
    height: 700px;
    .employee-notification{
      display: flex;
      align-items: flex-start;
      width: 100%;
      margin-bottom: 24px;
      margin-top: 20px;
      .avatar{
        width: 24px;
        height: 24px;
      }
      .information{
        margin-left: 10px;
        .who{
          .name{
            font-weight: 800;
            margin-right: 10px;
          }
          .time{
            color: #616061;
          }
        }
        .did{
          padding: 3px 10px;
          background-color: rgba(45, 156, 219, 0.1);
          border-radius: 6px;
          width: fit-content;
          font-weight: bold;
          font-size: 10px;
          text-transform: uppercase;
          line-height: 125%;
          text-align: center;
          letter-spacing: 0.05em;
          color: #2F80ED;
        }
        .task{
          font-weight: 500;
          line-height: 17px;
          letter-spacing: 0.38px;
          font-style: normal;
          margin-top: 10px;
        }
      }
    }
  }
  .more{
    padding: 10px 22px;
    display: flex;
    align-items: center;
    border-radius: 0px 0px 6px 6px;
    color: #FF4C00;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 13px;
    background-color: white;
    .arrow{
      margin-left: 20px;
      color: #FF4C00 !important;
    }
  }

}